.burger:hover {
  cursor: pointer;
}

.burger__icon {
  transition-duration: 0.5s;
  position: absolute;
  height: 2px;
  width: 30px;
  top: 50%;
  background-color: #343434;
}

.burger__icon:before {
  transition-duration: 0.5s;
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: #343434;
  content: "";
  top: -10px;
}

.burger__icon:after {
  transition-duration: 0.5s;
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: #343434;
  content: "";
  top: 10px;
}

.burger.active .burger__icon {
  transition-duration: 0.5s;
  background: transparent;
}

.burger.active .burger__icon:before {
  -webkit-transform: rotateZ(45deg) scaleX(1.25) translate(5px, 7px);
  transform: rotateZ(45deg) scaleX(1.25) translate(5px, 7px);
  background-color: #343434;
}

.burger.active .burger__icon:after {
  -webkit-transform: rotateZ(-45deg) scaleX(1.25) translate(6px, -8px);
  transform: rotateZ(-45deg) scaleX(1.25) translate(6px, -8px);
  background-color: #343434;
}

