.b-wysiwyg {
	width: 100%;
	margin: auto;
	position: relative;

	&__panel-boutons {
		position: relative;
		//padding: 5px
		//height: 41px
		width: 100%;
		background-color: $grey-light-2;
		@include row;
		align-items: center; }


	&__bouton-groupe {
		@include row;
		align-items: center; }

	&__btn {
		padding: 5px;

		height: 35px;
		width: 35px;
		font-size: 15px;
		background-color: transparent;
		border: none;
		outline: none;
		cursor: pointer;
		transition: all .6s;
		color: $font;
		svg {
			color: $font-light; }
		&:hover {
			color: #fff;
			background-color: $font;

			svg {
				color: #fff; } } }

	&__editeur {
		background-color: $grey-light;
		height: 150px;
		overflow-y: auto;
		padding: 10px;
		outline: none;
		font-size: 14px;

		a {
			color: $accent;
			text-decoration: underline; }

		ul {
			//@extend .b-list
			list-style: disc;
			padding-left: 20px;
			margin: 10px 0; }

		li {
			//@extend .b-list__item
			margin-bottom: 8px; } } }






