.e-icon {
	border: 2px solid $accent;
	background-color: rgba(255, 255, 255, .15);
	color: $dark;
	width: 36px;
	height: 36px;

	@include border-radius(50%);

	margin-right: 10px;

	display: inline-flex;
	align-items: center;
	justify-content: center;
	flex-shrink: 0;
	vertical-align: middle;
	&--white {
		border-color: #ffffff; }

	&--bord-red {
		border-color: $red; }
	&--bg-accent {
		background-color: $accent; }
	&--trsp {
		background-color: transparent; }
	&--disabled {
		border-color: $grey-darker;
		svg {
			color: $grey-darker; } }
	&--no-size {
		width: auto !important;
		height: auto !important; }
	&--right {
		margin-left: 10px;
		margin-right: 0 !important; }
	&--m0 {
		margin: 0 !important; }
	&--mr10 {
		margin-right: 10px !important; }
	&--mb {
		margin: 0 0 20px; }
	&--xxs {
		height: 18px;
		width: 18px;
		background-color: #ffffff; }
	&--xs {
		width: 26px;
		height: 26px; }
	&--sm {
		width: 28px;
		height: 28px; }
	&--md {
		width: 30px;
		height: 30px; }

	&--lg {
		width: 50px;
		height: 50px; }
	&--xxxl {
		width: 90px;
		height: 90px; }
	&--no-border {
		border: none;
		background: none; }
	&__figure {
		font-size: 16px;
		&--xxs {
			font-size: 8px !important; }
		&--xs {
			font-size: 14px !important; }
		&--sm {
			font-size: 18px; }
		&--xxl {
			font-size: 36px; }

		&--white {
			color: #ffffff; }
		&--text {
			font-family: $font-bold;
			font-size: 20px;
			font-style: normal;
			line-height: 0;
			&--light {
				font-family: $default-font; } }

		&--arrow {
			font-size: 8px; }
		&--credit-card {
			font-size: 17px; }

		&--assignment {
			font-size: 20px; }
		&--file {
			font-size: 18px; }
		&--comments {
			font-size: 19px; }
		&--thumb {
			font-size: 20px; }
		&--arrow-small {
			font-size: 8px; }
		&--plus {
			&--xs {
				font-size: 16px; } }
		&--close {
			font-size: 17px;
			//+rotate(45deg)
			&--xs {
				font-size: 18px; }
			&--xxs {
				font-size: 16px; } }
		&--edit--xs {
			font-size: 12px; }
		&--edit {
			font-size: 16px; }
		&--save {
			font-size: 12px; }
		&--timeout {
			color: $accent;
			font-size: 40px; }
		&--freevac-pictures {
			width: 18px;
			height: 18px;
			background: url(data:image/png;base64,) center no-repeat / contain; }

		&--check {
			font-size: 24px;
			&--xs {
				font-size: 11px; } }

		&--circle {
			color: $red; }
		&--monitor {
			width: 40px;
			height: 40px;

			background: url(data:image/png;base64,) center no-repeat / contain; }
		&--play {
			font-size: 40px;
			color: $orange;
			&--xs {
				font-size: 24px; } }
		&--arrow-pag {
			font-size: 25px; }
		&--trash {
			font-size: 25px; }
		&--freevac-icon-lg {
			font-size: 98px; }
		&--attach {
			font-size: 19px; }
		&--ellipsis {
			font-size: 30px; } }
	//.e-icon
	@media screen and (min-width: $lg) {
		width: 44px;
		height: 44px;
		margin-right: 12px;
		&--right {
			margin-left: 12px; }
		&--xxs {
			height: 18px;
			width: 18px; }
		&--xs {
			width: 26px;
			height: 26px; }
		&--sm {
			width: 28px;
			height: 28px; }
		&--md {
			width: 32px;
			height: 32px; }
		&--lg {
			width: 60px;
			height: 60px; }
		&--xxxl {
			width: 120px;
			height: 120px; }
		&__figure {
			font-size: 18px;
			&--sm {
				font-size: 16px; }
			&--xxl {
				font-size: 38px; }
			&--arrow {
				font-size: 9px; }
			&--credit-card {
				font-size: 19px; }
			&--assignment {
				font-size: 23px; }
			&--file {
				font-size: 24px; }
			&--comments {
				font-size: 23px; }
			&--thumb {
				font-size: 25px; }
			&--arrow-small {
				font-size: 8px; }
			&--save {
				font-size: 14px; }
			&--check {
				font-size: 28px;
				&--xs {
					font-size: 12px; } }
			&--close {
				font-size: 20px;
				&--xs {
					font-size: 18px; }
				&--xxs {
					font-size: 16px; } }
			&--camera {
				font-size: 20px; }
			&--eye-slash {
				font-size: 22px; }
			&--mobile {
				font-size: 24px; }
			&--monitor {
				width: 50px;
				height: 50px; }
			&--play {
				font-size: 40px;
				&--xs {
					font-size: 24px; } }
			&--arrow-pag {
				font-size: 25px; }
			&--trash {
				font-size: 25px; }
			&--plus {
				&--xs {
					font-size: 16px; } }
			&--edit--xs {
				font-size: 12px; }
			&--edit {
				font-size: 16px; }
			&--freevac-icon-lg {
				font-size: 98px; }
			&--attach {
				font-size: 19px; }
			&--ellipsis {
				font-size: 35px; } } }
	@media screen and (min-width: $xl) {
		width: 54px;
		height: 54px;
		margin-right: 19px;
		&--right {
			margin-left: 19px; }
		&--xxs {
			height: 18px;
			width: 18px; }
		&--xs {
			width: 26px;
			height: 26px; }
		&--sm {
			width: 32px;
			height: 32px; }
		&--md {
			width: 40px;
			height: 40px; }
		&--lg {
			width: 70px;
			height: 70px; }
		&--xxxl {
			width: 150px;
			height: 150px; }
		&__figure {
			font-size: 24px;
			&--sm {
				font-size: 18px; }
			&--xxl {
				font-size: 40px; }
			&--text {
				font-size: 20px; }
			&--video-service {
				font-size: 24px; }

			&--communication {
				font-size: 26px; }

			&--video-resume {
				font-size: 26px; }
			&--instruction {
				font-size: 31px; }
			&--music {
				font-size: 27px; }
			&--arrow {
				font-size: 11px; }
			&--credit-card {
				font-size: 25px;
				&--xxxl {
					font-size: 94px; } }
			&--assignment {
				font-size: 31px; }
			&--file {
				font-size: 29px; }
			&--comments {
				font-size: 31px; }
			&--thumb {
				font-size: 34px; }
			&--arrow-small {
				font-size: 8px; }
			&--save {
				font-size: 17px; }
			&--plus {
				font-size: 25px;
				&--xs {
					font-size: 16px; } }
			&--close {
				font-size: 20px;
				&--lg {
					font-size: 35px; }
				&--xs {
					font-size: 18px; }
				&--xxs {
					font-size: 16px; } }
			&--timeout {
				font-size: 40px; }
			&--backspace {
				font-size: 15px; }
			&--freevac-pictures {
				font-size: 18px; }
			&--check {
				font-size: 35px;
				&--xs {
					font-size: 13px; } }
			&--camera {
				font-size: 28px;
				&--sm {
					font-size: 18px; } }
			&--eye-slash {
				font-size: 27px; }
			&--mobile {
				font-size: 30px; }
			&--sound {
				font-size: 28px; }
			&--microphone {
				font-size: 36px; }
			&--stop {
				font-size: 17px; }
			&--circle {
				font-size: 28px; }
			&--monitor {
				width: 64px;
				height: 64px; }
			&--bag {
				font-size: 30px; }
			&--calendar {
				font-size: 28px; }
			&--tag {
				font-size: 27px; }
			&--privatbank {
				font-size: 25px; }
			&--play {
				font-size: 40px;
				&--xs {
					font-size: 24px; } }
			&--arrow-pag {
				font-size: 25px; }
			&--trash {
				font-size: 25px; }
			&--edit--xs {
				font-size: 12px; }
			&--edit {
				font-size: 16px; }
			&--freevac-icon-lg {
				font-size: 98px; }
			&--attach {
				font-size: 19px; }

			&--ellipsis {
				font-size: 40px; } } } }

.icon {
	&--auto {
		width: auto;
		height: auto; } }
