.b-social {
	@include row;

	&__item {
		display: inline-block;
		margin-right: 7px;
		&:last-child {
			margin-right: 0; } }


	&__icon-wrap {
		display: inline-flex;
		justify-content: center;
		align-items: center;

		width: 24px;
		height: 24px;
		vertical-align: middle;
		background-color: #fff;
		@include border-radius(50%); }


	&__icon {
		font-size: 16px;
		vertical-align: middle; }


	//.b-social
	@media screen and (min-width: $sm) {
		&__icon-wrap {
			width: 28px;
			height: 28px; } }
	@media screen and (min-width: $lg) {
		&__icon-wrap {
			width: 24px;
			height: 24px; } }
	@media screen and (min-width: $xl) {

		&__item {
			margin-right: 10px; } } }
