.o-welcome {
	position: relative;
	margin-top: -67px;
	@media screen and (min-width: $xl) {
		margin-top: -93px; }

	&__container {
		padding: 50px 15px;
		max-width: 88%;
		@media screen and (min-width: $lg) {
			padding: 150px 15px; } }

	&__item {
		min-height: 550px;

		background: transparent center / cover;

		display: none;

		align-items: center;
		justify-content: center;

		padding-bottom: 50px;

		padding-top: 67px;
		&.active {
			display: flex; }
		@media screen and (min-width: $lg) {
			min-height: 760px;
			padding-bottom: 80px; }
		@media screen and (min-width: $xl) {
			min-height: 845px;
			padding-bottom: 100px;
			padding-top: 93px; }

		@media screen and (min-width: $xxl) {
			min-height: calc(100% - 213px);
			min-height: calc(100vh - 213px); }


		@media screen and (min-height: $xl-g) {
			min-height: calc(100% - 213px);
			min-height: calc(100vh - 213px); } }



	&__content {

		padding: 50px 15px;
		@media screen and (min-width: $lg) {
			padding: 150px 15px; } }

	&__caption {
		text-align: center;
		text-transform: uppercase;
		@include text-shadow(2px, 3px, 3px, rgba(50, 45, 49, 0.2));

		color: $white-op;

		text-transform: uppercase;

		letter-spacing: 1.5px;

		margin-bottom: 40px; }
	&__btn-list {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;

		@media screen and (min-width: $sm) {
			flex-direction: row; } }
	&__btn-list-item {
		margin-bottom: 15px;
		@media screen and (min-width: $sm) {
			margin-bottom: 0;
			margin-right: 6%;
			&:last-child {
				margin-right: 0; } } }
	&__wel-info {
		@include row;

		margin-top: 16px; }
	&__video-wrap {

		@media screen and (max-width: $max-xl) {
			text-align: center;
			margin-bottom: 25px; }

		@media screen and (min-width: $xl) {
			padding-right: 50px; } } }
body .o-welcome {
	&__middle {
		margin: 50px 0;
		@media screen and (min-width: $lg) {
			margin: 75px 0; } } }

