.e-manipulation-btn {
	background-color: #ffffff;
	border: 2px solid $accent;
	padding: 9px 19px 9px;
	display: inline-block;
	@include border-radius(2em);
	&:hover {
		&__figure {
			color: $accent; } }
	&.__active {
		border-color: $grey-light-dark; }

	&__figure {
		@include transition(all, .4s, ease);
		&:hover {
			color: $accent; }
		&.__fill {
			color: $accent; } } }
